import React, { Component } from 'react';

import { Card, Row, Table, Col, message } from 'antd';

import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { ColEditDelete } from 'components/common';
import { getListCourse, deleteCourse } from 'services/ListCourse';
import { Link } from 'react-router-dom';
import routeConstant from 'routes/route/routeConstant';

const ROUTER_FORM_DATA = routeConstant.DANH_MUC__DAO_TAO__KHOA__NHAP_DU_LIEU;

class ListCourses extends Component {
  constructor(props) {
    super(props);
    this.state = { listCourses: [], loading: false, pagination: {} };
  }
  componentDidMount() {
    this.getListData();
  }

  getListData = page => {
    this.setState({
      loading: true,
    });
    getListCourse(page, true)
      .then(res => {
        const pagination = { ...this.state.pagination };
        pagination.total = res.data.totalResults;
        console.log('res.data.items', res.data.items);
        this.setState({
          listCourses: res.data.items,
          loading: false,
          pagination,
        });
      })
      .catch(error => {
        console.log('====>', error);
        this.setState({
          loading: false,
        });
      });
  };

  handleTableChange = pagination => {
    const pager = { ...this.state.pagination };
    pager.current = pagination.current;
    this.setState({
      pagination: pager,
    });
    this.getListData(pagination.current);
  };

  confirmDelete = e => {
    this.setState({
      loading: true,
    });
    deleteCourse(e)
      .then(() => {
        message.success('Xóa khóa đào tạo thành công');
        this.getListData();
        this.setState({
          loading: false,
        });
      })
      .catch(error => {
        this.setState({
          loading: false,
        });
        console.log('res', error);
        // message.error(error.response.data.reason);
      });
  };

  render() {
    const { listCourses, pagination, loading } = this.state;
    this.columns = [
      {
        title: 'Mã khóa',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tên khóa',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Từ năm',
        dataIndex: 'fromYear',
        key: 'fromYear',
      },
      {
        title: 'Đến năm',
        dataIndex: 'toYear',
        key: 'toYear',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: ROUTER_FORM_DATA,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.confirmDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Danh sách khóa đào tạo">
        <Row>
          <Col span={24}>
            <Link to={ROUTER_FORM_DATA}>
              <BtnAddNew />
            </Link>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              bordered
              columns={this.columns}
              dataSource={listCourses}
              pagination={pagination}
              onChange={this.handleTableChange}
              loading={loading}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListCourses />
  </Page>
);



